<template lang='pug'>
    div
        .parallax.parallax-screen.me-hero(style="background-position:initial" )
            div.li-header
                h1.li-header-text(style="font-family: Quattrocento;text-shadow: 1px 1px 3px #222222;") Hello Hooman!&#9;
                    img(src="../assets/nyan.gif" style="width:10%")
                hr.smaller
                h3.column-text.light(style="width:60%") Hi and welcome to my IP Assignment website. I am Abdul also known as YourFavouriteOreo in more geek circles.
                br
                br
                h3.column-text.light(style="width:60%") I tend to delve my time into different avenues of technology and I can't ever stick to something so I have dipped my toes into everything from Web development, App Development and even Game development (using Unreal Engine).
        .parallax.parallax-screen.handshake-hero(style="background-position:initial" )
            div.li-header
                h1.li-header-text(style="font-family: Quattrocento;text-shadow: 1px 1px 3px #222222;") How did I get my Internship?
                hr.smaller
                h3.column-text.light I applied , interviewed and done diddly did. (Jokes)
                br
                br
                h3.column-text.light Vertex is owned by a family relative and as such I had a network into the company. After conversations with him, he asked me for my work and was pleased with previous results I have had with other employers.
                br
                br
                h3.column-text.light After a few more conversations, we struck a deal on my internship pay, hours and I was locked in for my internship.
        .parallax.parallax-screen.parallax-hero(style="background-position:initial" )
            div.li-header
                h1.li-header-text(style="font-family: Quattrocento;text-shadow: 1px 1px 3px #222222;") My&nbsp;
                    span(style="font-family: Almendra Display;font-weight:bold") Internship Adventure
                hr.smaller
                h3.column-text.light
                    span My Internship is with an investment house in Dar es Salaam called&nbsp;
                        b Vertex Capital Limited.
                br
                br
                h3.column-text.light
                    span Vertex was looking into expanding themselves in various tech industries as well as cementing themselves digitally with websites to advertise themselves . I was brought in as a software engineer intern to start this process and the creation of their digital representation.
                br
                br
                h3.column-text.light I accepted the offer with Vertex Capital Limited due to not only seeing the potential in Vertex Capital Limited as a business but also for the impact it has in my community due to the fact that multiple of the businesses supported by Vertex improve or supply exclusively to the local communities and neighbouring countries.
                br
                br
                h3.column-text.light I also believed that I needed to work with a relatively young company to understand the struggles such companies go through as it is important to have a wide and diverse variety of perspective to ensure that all future software that I create accurately solves problems for companies of all shapes and sizes.
        .parallax.parallax-screen.meditation-hero(style="background-position:initial" )
            div.li-header
                h1.li-header-text(style="font-family: Quattrocento;text-shadow: 1px 1px 3px #222222;") Post-Internship Reflection
                hr.smaller
                h3.column-text.light
                    span Upon reflecting on my internship I think it was a success, reason being that my managers were impressed and I also manage to sink myself deeper into Vue.js which I used with all my VCL projects allowing me to further solidify my understand of both javascript and at least one of it's frameworks.
                br
                br
                h3.column-text.light However due to my role and the nature of the company with being self funded , I didn't manage to do much networking and to get my name out there to other potential future employers or just to form conenctions iwth other developers for future projects or quesitons.

</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
    }
  },
  components: {
  },
